body, html {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    font-family: 'Inter', sans-serif;
    background-color: #f8f9fa;
  }
  
  .chat-page {
    width: 100vw;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #ffffff;
  }
  
  .chat-header {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px 24px;
    border-bottom: 1px solid #e0e0e0;
    background-color: #ffffff;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.05);
  }
  
  .header-actions {
    display: flex;
    gap: 12px;
  }
  
  .icon-button {
    background: none;
    border: none;
    cursor: pointer;
    padding: 8px;
    border-radius: 50%;
    transition: background 0.2s;
  }
  
  .icon-button:hover {
    background: rgba(0, 0, 0, 0.05);
  }
  
  .tab-buttons {
    display: flex;
    width: 100%;
    justify-content: center;
    gap: 20px;
    padding: 12px 0;
    background-color: #ffffff;
  }
  
  .tab-button {
    padding: 10px 20px;
    border: none;
    background: none;
    font-size: 16px;
    cursor: pointer;
    border-bottom: 2px solid transparent;
    transition: border-color 0.2s;
  }
  
  .tab-button.active {
    border-color: #007bff;
    color: #007bff;
  }
  
  .search-container {
    display: flex;
    align-items: center;
    width: 90%;
    max-width: 600px;
    margin: 10px auto;
    padding: 8px;
    margin-top: 30px;
    border-radius: 24px;
    border: 1px solid #ddd;
    background: #fff;
  }
  
  .search-input {
    flex: 1;
    border: none;
    outline: none;
    padding: 8px;
    font-size: 14px;
  }
  
  .search-icon {
    margin: 0 10px;
    color: #666;
  }
  
  .friends-list {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 12px;
    padding: 16px;
  }
  
  .friend-card {
    width: 90%;
    max-width: 500px;
    display: flex;
    align-items: center;
    padding: 12px;
    background: #fff;
    border-radius: 12px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.05);
    cursor: pointer;
    transition: transform 0.2s;
  }
  
  .friend-card:hover {
    transform: scale(1.02);
  }
  
  .friend-avatar {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    overflow: hidden;
    position: relative;
    margin-right: 12px;
  }
  
  .friend-avatar img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .online-indicator {
    position: absolute;
    bottom: 4px;
    right: 4px;
    width: 12px;
    height: 12px;
    background: #4CAF50;
    border-radius: 50%;
    border: 2px solid #fff;
  }
  
  .friend-info {
    flex: 1;
  }
  
  .friend-info h3 {
    margin: 0;
    font-size: 16px;
    font-weight: 600;
  }
  
  .friend-info p {
    margin: 2px 0 0;
    font-size: 14px;
    color: #666;
  }
  
  .unread-badge {
    background: #007bff;
    color: #fff;
    font-size: 12px;
    font-weight: bold;
    padding: 4px 8px;
    border-radius: 12px;
  }
  