.onboarding-container {
    max-width: 500px;
    margin: 50px auto;
    padding: 20px;
    background: #f7f7f7;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  .onboarding-container h2 {
    margin-bottom: 20px;
    color: #333;
  }
  
  .onboarding-container form {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  
  .onboarding-container input,
  .onboarding-container select {
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: 16px;
  }
  
  .onboarding-container button {
    padding: 10px;
    background-color: #5cb85c;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
  }
  
  .onboarding-container button:hover {
    background-color: #4cae4c;
  }
  