/* Global Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Full-Screen Layout */
html, body, #root {
  height: 100%;
  width: 100%;
  background-color: #ffffff; /* Background set to white */
}

.friend-requests {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  background-color: #ffffff; /* Background set to white */
  font-family: 'Inter', sans-serif;
  overflow: hidden;
}

/* Header */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 1.5rem;
  background-color: #ffffff;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  position: sticky;
  top: 0;
  z-index: 10;
}

.back-button {
  background: none;
  border: none;
  padding: 0.5rem;
  cursor: pointer;
  color: #333;
  transition: opacity 0.2s;
}

.back-button:hover {
  opacity: 0.8;
}

.header h1 {
  font-size: 1.5rem;
  font-weight: 600;
  color: #333;
  margin: 0;
}

/* Main Content */
.requests-container {
  flex: 1;
  padding: 1.5rem;
  overflow-y: auto;
  background-color: #ffffff; /* Background set to white */
}

.no-requests {
  text-align: center;
  color: #666;
  font-size: 1rem;
  margin-top: 2rem;
}

/* Request Card */
.request-card {
  display: flex;
  align-items: center;
  background-color: #ffffff;
  padding: 1rem;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  margin-bottom: 1rem;
  transition: transform 0.2s, opacity 0.2s;
}

.request-card.processing {
  opacity: 0.6;
  pointer-events: none;
}

.avatar {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 1rem;
}

.user-details {
  flex: 1;
}

.user-details h3 {
  font-size: 1rem;
  font-weight: 600;
  margin: 0;
  color: #333;
}

.user-details p {
  font-size: 0.875rem;
  margin: 0;
  color: #666;
}

/* Action Buttons */
.action-buttons {
  display: flex;
  gap: 0.75rem;
}

.action-buttons button {
  background: none;
  border: none;
  padding: 0.5rem;
  border-radius: 50%;
  cursor: pointer;
  transition: background-color 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
}

.action-buttons button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.accept {
  color: #4CAF50;
}

.accept:hover {
  background-color: rgba(76, 175, 80, 0.1);
}

.reject {
  color: #f44336;
}

.reject:hover {
  background-color: rgba(244, 67, 54, 0.1);
}

/* Mobile Optimization */
@media (max-width: 480px) {
  .header {
    padding: 1rem;
  }

  .header h1 {
    font-size: 1.25rem;
  }

  .requests-container {
    padding: 1rem;
  }

  .request-card {
    padding: 0.75rem;
  }

  .avatar {
    width: 40px;
    height: 40px;
  }
}
