/* Global.css - Add this file to your project for complete dark mode support */

/* Reset CSS */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  /* Base styles with CSS variables */
  :root {
    --font-sans: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
    --font-mono: ui-monospace, SFMono-Regular, 'SF Mono', Menlo, Consolas, monospace;
    
    /* Light theme colors */
    --primary: #4a6cf7;
    --primary-hover: #3a56c5;
    --background: #ffffff;
    --foreground: #333333;
    --muted: #666666;
    --muted-foreground: #888888;
    --card: #ffffff;
    --card-foreground: #333333;
    --border: #e0e0e0;
    --input: #f5f7fa;
    --ring: rgba(74, 108, 247, 0.3);
    --radius: 0.5rem;
    
    /* System colors */
    --success: #10b981;
    --warning: #f59e0b;
    --danger: #ef4444;
    --info: #3b82f6;
  }
  
  /* Dark theme colors */
  [data-theme="dark"] {
    --primary: #5d7bf7;
    --primary-hover: #4a6cf7;
    --background: #121212;
    --foreground: #eaeaea;
    --muted: #a0a0a0;
    --muted-foreground: #a0a0a0;
    --card: #1a1a1a;
    --card-foreground: #eaeaea;
    --border: #3a3a3a;
    --input: #2a2a2a;
    --ring: rgba(93, 123, 247, 0.4);
    
    /* System colors for dark mode */
    --success: #34d399;
    --warning: #fbbf24;
    --danger: #f87171;
    --info: #60a5fa;
  }
  
  /* Apply base styles */
  html {
    font-family: var(--font-sans);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  body {
    background-color: var(--background);
    color: var(--foreground);
    font-feature-settings: "rlig" 1, "calt" 1;
    transition: background-color 0.3s, color 0.3s;
  }
  
  /* Focus styles */
  *:focus-visible {
    outline: 2px solid var(--primary);
    outline-offset: 2px;
  }
  
  /* Input styles */
  input, 
  textarea, 
  select {
    font-family: inherit;
    color: inherit;
  }
  
  /* Animations */
  @keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
  }
  
  @keyframes slideIn {
    from { transform: translateY(10px); opacity: 0; }
    to { transform: translateY(0); opacity: 1; }
  }
  
  /* Smooth scrolling */
  html {
    scroll-behavior: smooth;
  }
  
  /* Improve text selection */
  ::selection {
    background-color: var(--primary);
    color: white;
  }
  
  /* Custom scrollbar */
  ::-webkit-scrollbar {
    width: 8px;
    height: 8px;
  }
  
  ::-webkit-scrollbar-track {
    background: transparent;
  }
  
  ::-webkit-scrollbar-thumb {
    background-color: var(--border);
    border-radius: 4px;
  }
  
  ::-webkit-scrollbar-thumb:hover {
    background-color: var(--muted);
  }
  
  /* Container for app layout */
  .app-layout {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
  }
  
  /* Dark mode transition */
  .dark-mode-transition {
    transition: background-color 0.5s ease, color 0.5s ease, border-color 0.5s ease, box-shadow 0.5s ease;
  }