.voice-mode-container {
    position: fixed;
    bottom: 6rem;
    left: 50%;
    transform: translateX(-50%);
    background-color: #ffffff;
    border-radius: 1rem;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
    padding: 1.5rem;
    opacity: 0;
    transition: all 0.3s ease-in-out;
    pointer-events: none;
    min-width: 280px;
    z-index: 1000;
  }
  
  .voice-mode-container.active {
    opacity: 1;
    pointer-events: auto;
    transform: translateX(-50%) translateY(0);
  }
  
  .voice-mode-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
  }
  
  .voice-indicator {
    position: relative;
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #f3f4f6;
    transition: all 0.3s ease;
  }
  
  .voice-indicator.listening {
    background-color: #dbeafe;
  }
  
  .voice-indicator.speaking {
    background-color: #e0f2fe;
  }
  
  .voice-icon {
    width: 2rem;
    height: 2rem;
    color: #6b7280;
    transition: all 0.3s ease;
    z-index: 2;
  }
  
  .voice-indicator.listening .voice-icon {
    color: #3b82f6;
  }
  
  .voice-indicator.speaking .voice-icon {
    color: #0ea5e9;
  }
  
  /* Ripple effect for listening state */
  .ripple-container {
    position: absolute;
    width: 100%;
    height: 100%;
  }
  
  .ripple {
    position: absolute;
    border: 2px solid #3b82f6;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    animation: ripple 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
    opacity: 0;
  }
  
  .ripple:nth-child(2) {
    animation-delay: 0.5s;
  }
  
  .ripple:nth-child(3) {
    animation-delay: 1s;
  }
  
  @keyframes ripple {
    0% {
      transform: scale(0.8);
      opacity: 0;
    }
    50% {
      opacity: 0.4;
    }
    100% {
      transform: scale(1.4);
      opacity: 0;
    }
  }
  
  /* Sound waves animation for speaking state */
  .sound-waves {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 4px;
  }
  
  .wave {
    width: 3px;
    height: 20px;
    background-color: #0ea5e9;
    border-radius: 3px;
    animation: wave 1s ease-in-out infinite;
    transform-origin: bottom;
  }
  
  .wave:nth-child(2) {
    animation-delay: 0.2s;
  }
  
  .wave:nth-child(3) {
    animation-delay: 0.4s;
  }
  
  .wave:nth-child(4) {
    animation-delay: 0.6s;
  }
  
  @keyframes wave {
    0%, 100% {
      transform: scaleY(0.5);
    }
    50% {
      transform: scaleY(1);
    }
  }
  
  /* Voice status styling */
  .voice-status {
    text-align: center;
  }
  
  .status-text {
    font-size: 0.875rem;
    color: #374151;
    margin: 0;
    font-weight: 500;
  }
  
  .status-text.speaking {
    color: #0ea5e9;
  }
  
  .transcript-text {
    font-size: 0.875rem;
    color: #6b7280;
    margin: 0.5rem 0 0;
    max-width: 20rem;
    word-break: break-word;
    line-height: 1.4;
  }
  
  /* Listening indicator bars */
  .listening-indicator {
    display: flex;
    gap: 3px;
    height: 20px;
    align-items: center;
  }
  
  .listening-bar {
    width: 3px;
    height: 100%;
    background-color: #3b82f6;
    border-radius: 3px;
    animation: soundBar 1s ease-in-out infinite;
  }
  
  .listening-bar:nth-child(1) { animation-delay: 0.0s; }
  .listening-bar:nth-child(2) { animation-delay: 0.2s; }
  .listening-bar:nth-child(3) { animation-delay: 0.4s; }
  .listening-bar:nth-child(4) { animation-delay: 0.6s; }
  .listening-bar:nth-child(5) { animation-delay: 0.8s; }
  
  @keyframes soundBar {
    0%, 100% {
      transform: scaleY(0.3);
    }
    50% {
      transform: scaleY(1);
    }
  }