/* AddFriend.css */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  html, body, #root {
    height: 100%;
    width: 100%;
  }
  
  .add-friend-container {
    display: flex;
    flex-direction: column;
    height: 100vh;
    background-color: #ffffff;
    font-family: 'Inter', sans-serif;
  }
  
  .app-header {
    display: flex;
    align-items: center;
    padding: 1rem 1.5rem;
    background-color: #fff;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
    position: sticky;
    top: 0;
    z-index: 10;
  }
  
  .back-button {
    background: none;
    border: none;
    padding: 0.5rem;
    margin-right: 1rem;
    color: #333;
    cursor: pointer;
    transition: opacity 0.2s;
  }
  
  .back-button:hover {
    opacity: 0.8;
  }
  
  .app-header h1 {
    font-size: 1.25rem;
    font-weight: 600;
    color: #333;
  }
  
  .search-form {
    padding: 1.5rem;
    border-bottom: 1px solid #eee;
  }
  
  .search-group {
    display: flex;
    gap: 0.5rem;
  }
  
  .search-input {
    flex: 1;
    padding: 0.75rem 1rem;
    border: 1px solid #e0e0e0;
    border-radius: 8px;
    font-size: 1rem;
    transition: border-color 0.2s;
  }
  
  .search-input:focus {
    outline: none;
    border-color: #007bff;
  }
  
  .search-button {
    padding: 0.75rem 1.5rem;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: opacity 0.2s;
  }
  
  .search-button:disabled {
    opacity: 0.7;
    cursor: not-allowed;
  }
  
  .results-container {
    flex: 1;
    padding: 0 1.5rem;
    overflow-y: auto;
  }
  
  .user-card {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem 0;
    border-bottom: 1px solid #eee;
  }
  
  .user-info {
    display: flex;
    align-items: center;
    gap: 1rem;
  }
  
  .user-avatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
  }
  
  .user-details {
    flex: 1;
  }
  
  .user-name {
    font-size: 1rem;
    font-weight: 500;
    color: #333;
  }
  
  .user-email {
    font-size: 0.875rem;
    color: #666;
  }
  
  .action-button {
    width: 40px;
    height: 40px;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background-color 0.2s;
  }
  
  .send-button {
    background-color: #4CAF50;
    color: white;
  }
  
  .send-button:hover {
    background-color: #45a049;
  }
  
  .cancel-button {
    background-color: #f44336;
    color: white;
  }
  
  .cancel-button:hover {
    background-color: #e53935;
  }
  
  .no-results {
    text-align: center;
    padding: 2rem 0;
    color: #666;
  }
  
  @media (max-width: 480px) {
    .app-header {
      padding: 1rem;
    }
  
    .search-form {
      padding: 1rem;
    }
  
    .search-group {
      flex-direction: column;
    }
  
    .search-button {
      width: 100%;
    }
  
    .results-container {
      padding: 0 1rem;
    }
  
    .user-card {
      padding: 0.75rem 0;
    }
  }
  
  @keyframes fadeIn {
    from { opacity: 0; transform: translateY(10px); }
    to { opacity: 1; transform: translateY(0); }
  }
  
  .user-card {
    animation: fadeIn 0.3s ease-out;
  }