.navigation {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 3.5rem;
  background-color: #ffffff;
  border-bottom: 1px solid #e5e7eb;
  display: flex;
  align-items: center;
  padding: 0 1rem;
}

.navigation.dark {
  background-color: #1f2937;
  border-bottom-color: #374151;
}

.navigation .menu-button,
.navigation .options-button {
  padding: 0.5rem;
  border-radius: 0.375rem;
  cursor: pointer;
}

.navigation .menu-button:hover,
.navigation .options-button:hover {
  background-color: #e5e7eb;
}

.navigation .menu-button.dark:hover,
.navigation .options-button.dark:hover {
  background-color: #4b5563;
}

.navigation .title {
  flex: 1;
  text-align: center;
  font-weight: bold;
  font-size: 1rem;
}
